import React from 'react';
import PropTypes from 'prop-types';
import {
    Wrapper,
    GradeLine,
    GrageRange,
    Box,
} from './styled';

/**
 * @develop
 */
export default class RatingNPS extends React.Component {
    static propTypes = {
        /**
         *   
         */
        minGrade: PropTypes.string,
        /**
         *   
         */
        maxGrade: PropTypes.string,
        /**
         *  
         */
        countOfBoxes: PropTypes.number,
        /**
         *  
         */
        rateValue: PropTypes.number,
        getValue: PropTypes.func.isRequired,
    };

    static defaultProps = {
        minGrade: '',
        maxGrade: '  ',
        countOfBoxes: 11,
        rateValue: 0,
    };

    state = {
        rateValue: this.props.rateValue || null,
        isRated: false,
    };

    onClick = (index) => {
        const { getValue } = this.props;
        const {
            isRated,
            rateValue,
        } = this.state;
        this.setState({
            rateValue: index,
            isRated: rateValue === index ? !isRated : true,
        });
        getValue(index);
    };

    render() {
        const { isRated, rateValue } = this.state;
        const { minGrade, maxGrade, countOfBoxes } = this.props;

        return (
            <Wrapper>
                <GradeLine>
                    {[...Array(countOfBoxes)].map((item, index) => (
                        <Box
                            key={item}
                            countOfBoxes={countOfBoxes}
                            isRated={isRated}
                            rateValue={rateValue}
                            onClick={() => this.onClick(index)}
                            value={index}
                            disabled={isRated}
                        >
                            {index}
                        </Box>
                    ))}
                </GradeLine>
                <GrageRange>
                    <span>{minGrade}</span>
                    <span>{maxGrade}</span>
                </GrageRange>
            </Wrapper>
        );
    }
}
