import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import addEventListener from 'rc-util/lib/Dom/addEventListener';
import RcDialog from 'rc-dialog';
import Button from '../Button';
import GlobalStyles from './styled';


/**
 *  
 * @develop
 */
export default class Modal extends React.Component {
    static propTypes = {
        /**
         *   
         */
        onOk: PropTypes.func,
        /**
         *   
         */
        onCancel: PropTypes.func,
        /**
         *      
         */
        okText: PropTypes.node,
        /**
         *     
         */
        cancelText: PropTypes.node,
        /**
         *   
         */
        okColor: PropTypes.string,
        /**
         *   
         */
        width: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
        /**
         *     
         */
        okLoading: PropTypes.bool,
        /**
         *  
         */
        visible: PropTypes.bool,
        /**
         *   
         */
        footer: PropTypes.node,
        /**
         * 
         */
        title: PropTypes.node,
        /**
         *   ()   
         */
        closable: PropTypes.bool,
        /**
         *   
         */
        verticalCentered: PropTypes.bool,
        /**
         *   body, footer
         */
        extendedContent: PropTypes.bool,
        /**
         *   
         */
        className: PropTypes.string,
        /**
         *   
         */
        wrapClassName: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    };

    static defaultProps = {
        okColor: 'blue',
        width: 520,
        okLoading: false,
        visible: false,
        verticalCentered: false,
        extendedContent: false,
        onOk() {},
        onCancel() {},
        okText: undefined,
        cancelText: undefined,
        footer: undefined,
        title: undefined,
        closable: true,
        className: '',
        wrapClassName: '',
    };

    mousePosition = {};

    mousePositionEventBinded = null;

    componentDidMount() {
        if (this.mousePositionEventBinded) {
            return;
        }
        addEventListener(document.documentElement, 'click', (e) => {
            this.mousePosition = {
                x: e.pageX,
                y: e.pageY,
            };
            setTimeout(() => {
                this.mousePosition = null;
            }, 100);
        });
        this.mousePositionEventBinded = true;
    }

    onCancel = (e) => {
        const { onCancel } = this.props;
        if (onCancel) {
            onCancel(e);
        }
    };

    onOk = (e) => {
        const { onOk } = this.props;
        if (onOk) {
            onOk(e);
        }
    };

    renderFooter() {
        const {
            okText,
            okColor,
            cancelText,
            okLoading,
        } = this.props;
        return (
            <div>
                <Button
                    color="gray"
                    side={40}
                    onClick={this.onCancel}
                >
                    {cancelText || ''}
                </Button>
                <Button
                    color={okColor}
                    side={40}
                    onClick={this.onOk}
                    loading={okLoading}
                >
                    {okText || ''}
                </Button>
            </div>
        );
    }

    render() {
        const {
            footer,
            visible,
            wrapClassName,
            verticalCentered,
            extendedContent,
            ...other
        } = this.props;
        const defaultFooter = this.renderFooter();

        return (
            <React.Fragment>
                <RcDialog
                    {...other}
                    prefixCls="ui-modal"
                    transitionName="ui-modal-zoom"
                    maskTransitionName="ui-modal-fade"
                    wrapClassName={classNames(
                        wrapClassName,
                        {
                            'ui-modal-vertical-centered': verticalCentered,
                            'ui-modal-extended-content': extendedContent,
                        },
                    )}
                    footer={typeof footer === 'undefined' ? defaultFooter : footer}
                    visible={visible}
                    mousePosition={this.mousePosition}
                    onClose={this.onCancel}
                />
                <GlobalStyles />
            </React.Fragment>
        );
    }
}
