import React from 'react';
import PropTypes from 'prop-types';
import MaskedInput from '../MaskedInput';
import {
    Field,
    IconWrapper,
    Wrapper,
    Inner,
    FieldWrapper,
    Addon,
} from './styled';


/**
 * @develop
 */
export default class Input extends React.Component {
    static propTypes = {
        /**
         *  ,      
         * ,  'number'     ,
         *     'selectionStart'  'selectionEnd'.
         * : http://w3c.github.io/html/sec-forms.html#does-not-apply
         */
        type: PropTypes.oneOf([
            'hidden',
            'password',
            'text',
            'email',
            'tel',
            'url',
            'number',
            'card',
            'money',
        ]),
        /**
         *  
         */
        value: PropTypes.string,
        /**
         *   
         */
        autoComplete: PropTypes.bool,
        /**
         *   
         */
        maxLength: PropTypes.number,
        /**
         *   
         */
        id: PropTypes.string,
        /**
         *   
         */
        name: PropTypes.string,
        /**
         *        Tab
         */
        tabIndex: PropTypes.number,
        /**
         *   HTMLInputElement 'pattern'.
         *          .
         */
        pattern: PropTypes.string,
        /**
         *          
         */
        formNoValidate: PropTypes.bool,
        /**
         * ,    
         */
        title: PropTypes.string,
        /**
         *   
         */
        placeholder: PropTypes.string,
        /**
         *  
         */
        disabled: PropTypes.bool,
        /**
         *  .       
         */
        status: PropTypes.oneOf(['default', 'success', 'error']),
        /**
         * 
         */
        icon: PropTypes.node,
        /**
         *  
         */
        iconPosition: PropTypes.oneOf(['before', 'after']),
        /**
         *  
         */
        boundless: PropTypes.bool,
        /**
         *      
         */
        centered: PropTypes.bool,
        /**
         *      
         */
        leftAddons: PropTypes.node,
        /**
         *      
         */
        rightAddons: PropTypes.node,
        /**
         *  
         */
        largeFont: PropTypes.bool,
        /**
         *     . [ ](https://github.com/insin/inputmask-core#pattern
         */
        mask: PropTypes.string,
        /**
         *    , 
         *  formatCharacters  `inputmask-core`
         */
        maskFormatCharacters: PropTypes.shape({
            validate: PropTypes.func.isRequired,
            transform: PropTypes.func,
        }),
        /**
         *    
         */
        onFocus: PropTypes.func,
        /**
         *     
         */
        onBlur: PropTypes.func,
        /**
         *   
         */
        onChange: PropTypes.func,
        /**
         *    
         */
        onClick: PropTypes.func,
        /**
         *        ,
         *     
         */
        onKeyDown: PropTypes.func,
        /**
         *        ,
         *     
         */
        onKeyUp: PropTypes.func,
        /**
         *      
         */
        onPaste: PropTypes.func,
        /**
         *     
         */
        onTouchStart: PropTypes.func,
        /**
         *      
         */
        onTouchEnd: PropTypes.func,
        /**
         *       
         */
        onTouchMove: PropTypes.func,
        /**
         *      
         */
        onTouchCancel: PropTypes.func,
        /**
         * ,       
         */
        onProcessMaskInputEvent: PropTypes.func,
        style: PropTypes.shape(),
        className: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
        'data-testid': PropTypes.string,
    };

    static defaultProps = {
        formNoValidate: false,
        value: undefined,
        autoComplete: false,
        maxLength: undefined,
        id: undefined,
        name: undefined,
        tabIndex: undefined,
        pattern: undefined,
        title: undefined,
        placeholder: undefined,
        disabled: undefined,
        status: undefined,
        type: 'text',
        icon: null,
        iconPosition: 'before',
        boundless: false,
        centered: false,
        leftAddons: null,
        rightAddons: null,
        largeFont: false,
        mask: undefined,
        maskFormatCharacters: undefined,
        onFocus: undefined,
        onBlur: undefined,
        onChange: undefined,
        onClick: undefined,
        onKeyDown: undefined,
        onKeyUp: undefined,
        onPaste: undefined,
        onTouchStart: undefined,
        onTouchEnd: undefined,
        onTouchMove: undefined,
        onTouchCancel: undefined,
        onProcessMaskInputEvent: undefined,
        style: undefined,
        className: undefined,
        'data-testid': undefined,
    };

    state = {
        isFocused: false,
    };

    root = null;

    onRef = (root) => {
        this.root = root;
    };

    onFocus = (event) => {
        const { onFocus } = this.props;
        this.setState({ isFocused: true });
        if (onFocus) {
            onFocus(event);
        }
    };

    onBlur = (event) => {
        const { onBlur } = this.props;
        this.setState({ isFocused: false });
        if (onBlur) {
            onBlur(event);
        }
    };

    onChange = (event) => {
        this.changeValue(event.target.value);
    };

    onClick = (event) => {
        const { onClick } = this.props;
        if (onClick) {
            onClick(event);
        }
    };

    onKeyDown = (event) => {
        const { onKeyDown } = this.props;
        if (onKeyDown) {
            onKeyDown(event);
        }
    };

    onKeyUp = (event) => {
        const { onKeyUp } = this.props;
        if (onKeyUp) {
            onKeyUp(event);
        }
    };

    onPaste = (event) => {
        const { onPaste } = this.props;
        if (onPaste) {
            onPaste(event);
        }
    };

    onTouchStart = (event) => {
        const { onTouchStart } = this.props;
        if (onTouchStart) {
            onTouchStart(event);
        }
    };

    onTouchEnd = (event) => {
        const { onTouchEnd } = this.props;
        if (onTouchEnd) {
            onTouchEnd(event);
        }
    };

    onTouchMove = (event) => {
        const { onTouchMove } = this.props;
        if (onTouchMove) {
            onTouchMove(event);
        }
    };

    onTouchCancel = (event) => {
        const { onTouchCancel } = this.props;
        if (onTouchCancel) {
            onTouchCancel(event);
        }
    };

    getMaskedInputInstance() {
        const { mask } = this.props;
        if (typeof mask !== 'undefined') {
            return this.root;
        }
        return null;
    }

    changeValue(value) {
        const { onChange } = this.props;
        if (onChange) {
            onChange(value);
        }
    }

    isMaskFilled() {
        const { mask } = this.props;
        if (typeof mask !== 'undefined') {
            return this.root.isFilled();
        }
        return false;
    }

    focus() {
        this.root.focus();
    }

    blur() {
        this.root.blur();
    }

    render() {
        const {
            type,
            value,
            formNoValidate,
            autoComplete,
            maxLength,
            id,
            name,
            tabIndex,
            pattern,
            title,
            placeholder,
            disabled,
            status,
            icon,
            iconPosition,
            boundless,
            centered,
            leftAddons,
            rightAddons,
            largeFont,
            mask,
            maskFormatCharacters,
            onProcessMaskInputEvent,
            className,
            style,
            'data-testid': dataTestId,
        } = this.props;
        const { isFocused } = this.state;
        const isMaskedInput = typeof mask !== 'undefined';
        const inputProps = {
            ref: this.onRef,
            type,
            value,
            formNoValidate,
            autoComplete: autoComplete ? 'on' : 'off',
            maxLength,
            id,
            name,
            tabIndex,
            pattern,
            title,
            placeholder,
            disabled,
            'data-testid': dataTestId,
            onChange: this.onChange,
            onFocus: this.onFocus,
            onBlur: this.onBlur,
            onClick: this.onClick,
            onKeyDown: this.onKeyDown,
            onKeyUp: this.onKeyUp,
            onPaste: this.onPaste,
            onTouchStart: this.onTouchStart,
            onTouchEnd: this.onTouchEnd,
            onTouchMove: this.onTouchMove,
            onTouchCancel: this.onTouchCancel,
        };

        return (
            <Wrapper
                focused={(!value || value.length === 0) && isFocused}
                filling={value && value.length > 0 && isFocused}
                status={status}
                disabled={disabled}
                boundless={boundless}
                className={className}
                style={style}
            >
                <Inner>
                    {leftAddons && (
                        <Addon leftGutter>{leftAddons}</Addon>
                    )}
                    {icon !== null && (
                        <IconWrapper
                            iconBefore={icon && iconPosition === 'before'}
                            iconAfter={icon && iconPosition === 'after'}
                        >
                            {icon}
                        </IconWrapper>
                    )}
                    <FieldWrapper>
                        <Field
                            as={isMaskedInput ? MaskedInput : 'input'}
                            large={largeFont}
                            centered={centered}
                            iconBefore={icon && iconPosition === 'before'}
                            iconAfter={icon && iconPosition === 'after'}
                            {...inputProps}
                            /*    MaskedInput */
                            mask={mask}
                            formatCharacters={maskFormatCharacters}
                            onProcessInputEvent={onProcessMaskInputEvent}
                        />
                    </FieldWrapper>
                    {rightAddons && (
                        <Addon rightGutter>{rightAddons}</Addon>
                    )}
                </Inner>
            </Wrapper>
        );
    }
}
